<template>
  <div class="home">
    <img alt="Vue logo" src="../assets/logo.png" />
  </div>
</template>

<script>
// @ is an alias to /src

var box = document.querySelector(".box");
var btn = document.querySelector("button");
var val = document.querySelector(".val");
var yy = 0;
var timer = setInterval(function () {
  yy += 6;
  box.style.transform =
    "translateY(" + yy + "px) rotateY(132deg) rotateX(32deg)";
  if (yy == 330) {
    clearInterval(timer);
  }
}, 10);
document.body.onmousedown = function (e) {
  x = e.clientX;
  y = e.clientY;

  document.onmousemove = function (e) {
    var x1 = e.clientX - x - 20;
    var y1 = e.clientY - y - 20;
    box.style.transform =
      "translateY(" + yy + "px) rotateY(" + x1 + "deg) rotateX(" + -y1 + "deg)";
  };

  document.onmouseup = function () {
    document.onmousemove = null;
  };
};

btn.onclick = playTheGame;
box.addEventListener("webkitAnimationEnd", () => {
  box.style.animationName = "none"; // 更改动画属性，以待下一次动画的正常执行
  // 可能出现的情况集合
  let _posible = [
    { value: 3, x: 0, y: 0 },
    { value: 4, x: 0, y: 180 },
    { value: 5, x: 0, y: -90 },
    { value: 2, x: 0, y: 90 },
    { value: 1, x: -90, y: 0 },
    { value: 6, x: 90, y: 0 },
  ];
  // 准备抽取的随机数
  let _random = Math.floor(Math.random() * 6);
  // 抽取的随机结果
  let _result = _posible[_random];
  setTimeout(() => {
    // 浏览器反应不过来加过渡
    // 让骰子旋转到正确的角度
    box.style.transform = `translateY(330px) rotateX(${_result.x}deg) rotateY(${_result.y}deg)`;
    res(_result.value); // 渲染视图
  }, 0);
});

function playTheGame() {
  // 游戏方法
  // 骰子转起来

  // 有的时候浏览器在连续使用js操作css的时候会出现问题（反应不过来），比如，效果不能正常显示，此时可以尝试利用setTimeout-0来将目标代码放入到异步队列中等待执行
  setTimeout(() => {
    box.style.animationName = "rotate";
  }, 0);
}

function res(res) {
  val.innerHTML = `您的结果为:${res}`;
}
</script>
